import React, { useState } from 'react';

/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react';
import SqlGuide from 'components/post/SqlGuide';
import { Box, IconButton, List, ListItem, ListItemButton, ListItemText, SwipeableDrawer } from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';

const containerStyle = css`

`


const list = [
  {title: "SQL ", comp: <SqlGuide/>},
  {title: "test", comp: <div>
    <p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p><p>test</p>
  </div>}
]

const getComponent = (index) => {
  if(index === -1) return <div></div>
  
  if(list[index]) {
    return list[index].comp;
  } else {
    return <div></div>
  }
}


const Post = () => {
  const [drawerOpen, setDrawerOpen] = useState(false);
  const [listIndex, setListIndex] = useState(-1);
  
  // const toggleDrawer = (val) => {
  //   setDrawerOpen(val)
  // }

  const openDrawer = (index) => {
    setListIndex(index);
    setDrawerOpen(true);
  }

  const closeDrawer = () => {
    setDrawerOpen(false);
  }

  return (
    <div css={containerStyle}>
      <List>
        {list.map((item, index) => (
          <ListItem disablePadding key={index}>
            <ListItemButton onClick={() => {openDrawer(index)}}>
              <ListItemText primary={item.title} />
            </ListItemButton>
          </ListItem>
        ))}
      </List>
      <SwipeableDrawer
        anchor="bottom"
        open={drawerOpen}
        onClose={() => {closeDrawer()}}
        onOpen={() => {openDrawer(0)}}
        swipeAreaWidth={20}
        disableSwipeToOpen={false}
        keepMounted
      >
        <Box sx={{minHeight: "100vh"}}>
          <IconButton color="inherit" sx={{position: "fixed", top:10, right: 20}}
                      aria-label="close" size="large"
                      onClick={() => {closeDrawer()}}>
            <CloseIcon fontSize="inherit" />
          </IconButton>
          <Box sx={{ marginTop: "64px"}}>
            {getComponent(listIndex)}
          </Box>
        </Box>
      </SwipeableDrawer>
    </div>
  )
}
export default Post;