import React, {useEffect, useState, useRef} from 'react';

import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableRow from '@mui/material/TableRow';

import { maxWidth, minWidth } from 'values/styleValue';

/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react';
import styled from '@emotion/styled';

import dayjs from 'dayjs';


const containerStyle = css`

`
const tableStyle = css`
  max-width: 600px;
  margin: 0 auto;
`
const tableRowStyle = css`

  @media screen and (min-width: ${minWidth}px){
    height: 75px;
  }
`
const tableCellStyle = css`
  font-weight: bold;
  text-align: center;
  /* padding: 10px; */
  @media screen and (max-width: ${maxWidth}px ) {
    padding: 10px;
  }

  @media screen and (min-width: ${minWidth}px){
    font-size: 18px;
  }
`


const Calendar = () => {
  const [calendar, setCalendar] = useState([]);
  const refToday = useRef('');

  useEffect(() => {
    createCalendar();
    let interval = setInterval(() => {
      createCalendar();
    }, 1000)
    return () => {
      clearInterval(interval);
    }
  }, [])

  /**
   *  
   */
  const createCalendar = () => {
    let today = dayjs();

    let todayString = today.format('YYYYMMDD');
    if(todayString === refToday.current) {
      return;
    }
    
    refToday.current = todayString;

    let firstDay = today.set('date', 1);
    let lastDay = firstDay.add(1, 'month').add(-1, 'day');

    let days = [];

    //   
    for(let i=0; i<7; i++) {
      if(i === firstDay.get('day')) {
        break;
      } else {
        days.push('')
      }
    }

    // 1~ 
    let yearMonth = firstDay.format('YYYYMM')
    for(let i=1; i<=lastDay.get('date'); i++) {
      days.push(yearMonth + String(i).padStart(2, '0'));
    }

    //   
    for(let i=0; i<7; i++) {
      if(i > lastDay.get('day')) {
        days.push('')
      }
    }

    days.reverse();

    let calendar = []
    let weeksize = days.length / 7;
    for(let i=0; i<weeksize; i++) {
      let week = []
      for(let j=0; j<7; j++) {
        week.push(days.pop())
      }
      calendar.push(week);
    }

    setCalendar(calendar)
  }

  return (
    <div css={containerStyle}>
      <Table css={tableStyle} aria-label="calendar table">
        <TableBody>
          {calendar.map((week, weekNo) => (
            <TableRow key={weekNo} css={tableRowStyle}>
              {week.map((day, dayNo) => {
                let color = 'white';
                let background = '';
                
                if(dayNo === 0) {
                  color = '#c33';
                } else if (dayNo === 6) {
                  color = '#33c';
                }
                if(refToday.current === day) {
                  // border = '3px solid'
                  color = '#111';
                  background = '#666';
                }

                return <TableCell css={tableCellStyle} sx={{ color: color, background: background }} key={dayNo}>
                  {day.substr(6, 2)}
                </TableCell>
              })}
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </div>
  )
}
export default Calendar;